import React from "react";
import "../assets/css/Skills.css";
import { Waypoint } from "react-waypoint";
import PropTypes from "prop-types";
import { useState } from "react";

function Skills() {
  return (
    <div className="skills container">
      <div className="section-title">
        <h2>Skills</h2>
      </div>

      <div className="row skills-content">
        <div className="col-lg-6">
          <ProgressBar name="Linux" value={100} />
          <ProgressBar name="HTML" value={98} />
          <ProgressBar name="CSS" value={78} />
          <ProgressBar name="JavaScript" value={89} />
        </div>
        <div className="col-lg-6">
          <ProgressBar name="PHP" value={82} />
          <ProgressBar name="Wordpress/CMS" value={95} />
          <ProgressBar name="Photoshop" value={57} />
          <ProgressBar name="Python" value={98} />
        </div>
      </div>
    </div>
  );
}

function ProgressBar(props) {
  const _updateProgress = () => {
    setSkillValue(props.value);
  };

  const [skillValue, setSkillValue] = useState(0);
  return (
    <Waypoint onEnter={() => _updateProgress()}>
      <div className="progress">
        <span className="skill">
          {props.name} <i className="val">{props.value}%</i>
        </span>
        <div className="progress-bar-wrap">
          <div
            className="progress-bar"
            role="progressbar"
            style={{ width: skillValue + "%" }}
            aria-valuenow={props.value}
            aria-valuemin="0"
            aria-valuemax="100"
          ></div>
        </div>
      </div>
    </Waypoint>
  );
}

ProgressBar.propTypes = {
  name: PropTypes.string.isRequired,
  value: PropTypes.number.isRequired,
};

export default Skills;
